@import '../../scss';

$drawer-width: (
  'mobile':  100%,
  'm':  p-px-to-rem(336px),
  'l':  p-px-to-rem(405px),
  'xxl':  p-px-to-rem(606px),
  'default': p-px-to-rem(330px)
);

:host {
  float: right;
  left: -1000px;
  width: fit-content;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  height: 100vh;
  z-index: $phn-z-index-mega-flyout + 1;

  &.slide-in-from-left {
    animation: slide-in-from-left-absolute 600ms;
  }

  &.slide-out-to-left {
    animation: slide-in-from-left-absolute 600ms reverse;
  }

  @include pds-media-query-min-max('base', 's') {
    width: map-get($drawer-width, 'mobile');
  }

  .contextual-drawer-backdrop {
    position: fixed;
    z-index: $phn-z-index-mega-flyout;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.0);
    opacity: 0;
    cursor: pointer;
    @include pds-frosted-glass();

    &.fade-in-animation {
      animation: fade-in-animation 400ms;
    }

    &.fade-out-animation {
      animation: fade-in-animation 400ms reverse;
    }

  }

  .close-button {
    $button-width: 48px;
    $button-margin: 28px;
    position: absolute;
    right: p-px-to-rem(-($button-width + $button-margin));
    z-index: $phn-z-index-mega-flyout + 1;
    width: p-px-to-rem($button-width);
    height: p-px-to-rem($button-width);
    margin-top: p-px-to-rem($button-margin);
    cursor: pointer;

    @include pds-media-query-min-max('base', 's') {
      display: none;
    }
  }

  .contextual-drawer{
    background-color: $main-drawer-background-color;
    position: relative;
    overflow-y: auto;
    overflow-x: hidden;
    width: map-get($drawer-width, 'default');
    border-radius: 0 4px 4px 0;
    height: 100vh;
    z-index: $phn-z-index-mega-flyout + 1;

    @include pds-media-query-min-max('base', 's') {
      width: map-get($drawer-width, 'mobile');
      border-radius: 0;
      background-color: $main-drawer-mobile-background-color;
    }

    @include pds-media-query-min('m') {
      width: map-get($drawer-width, 'm');
    }

    @include pds-media-query-min('l') {
      width: map-get($drawer-width, 'l');
    }

    @include pds-media-query-min('xxl') {
      width: map-get($drawer-width, 'xxl');
    }
  }


}
