import { Component, Event, EventEmitter, h, Host, JSX, Listen, State } from '@stencil/core';
import {
  registerContextualDrawerClosing,
  registerContextualDrawerOpening
} from './contextual-drawer-animation-sequence';
import animationManager from '../../services/animations/animation-manager';
import { DrawerStatusTypes, DrawerStatusValues } from '../../utility/constants/app';

@Component({
  tag: 'contextual-drawer',
  styleUrl: 'contextual-drawer.scss',
  scoped: true
})
export class ContextualDrawer {
  @Event() openBackdrop!: EventEmitter<boolean>;
  @State() isDrawerOpened = false;

  @Event({ composed: true, bubbles: true }) phnContextualDrawerStatus!: EventEmitter<{
    status: DrawerStatusTypes;
  }>;

  @Listen('phnCloseContextualDrawer', { target: 'window' })
  closeContextualDrawer(): void {
    this.closeDrawer();
  }

  @Listen('phnOpenContextualDrawer', { target: 'window' })
  openContextualDrawer(): void {
    animationManager.play('openContextualDrawer');
  }

  private closeDrawer(): void {
    animationManager.play('closeContextualDrawer').then(() => {
      this.phnContextualDrawerStatus.emit({ status: DrawerStatusValues.finishClosing });
      this.isDrawerOpened = false;
    });
  }

  componentDidLoad(): void {
    registerContextualDrawerOpening({
      onStartOpening: () => {
        this.phnContextualDrawerStatus.emit({ status: DrawerStatusValues.startOpening });
        this.isDrawerOpened = true;
        const contextualDrawerHeader = document.getElementById('contextual-drawer-header');
        if (contextualDrawerHeader) {
          contextualDrawerHeader.style.display = '';
        }
      },
      onFinishOpening: () => {
        this.phnContextualDrawerStatus.emit({ status: DrawerStatusValues.finishOpening });
      }
    });

    registerContextualDrawerClosing({
      onStartClosing: () => {
        this.phnContextualDrawerStatus.emit({ status: DrawerStatusValues.startClosing });
      }
    });
  }

  render(): JSX.Element {
    return (
      <Host style={{ display: this.isDrawerOpened ? '' : 'none' }}>
        <phn-p-button
          variant="secondary"
          className="close-button"
          theme="dark"
          hide-label="true"
          icon={'close'}
          aria-label="close modal dialog"
          onClick={() => {
            this.closeDrawer();
          }}
        />
        <div class="contextual-drawer" id="contextual-drawer">
          <div slot="contextual_drawer_header">
            <slot name="contextual_drawer_header"></slot>
          </div>
        </div>
        <div
          aria-hidden="true"
          class="contextual-drawer-backdrop"
          onClick={() => {
            this.closeDrawer();
          }}
        />
      </Host>
    );
  }
}
